@tailwind base;
@tailwind components;
@tailwind utilities;

 
  
::-webkit-scrollbar {
    width: 10px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
  

::-webkit-scrollbar-track {
    background: #0f172a;
}


::-webkit-scrollbar-thumb {
    background: #1e293b;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}


::-webkit-scrollbar-thumb:hover {
    background: #555;
    border-radius: 10px;
}

@layer components {
    .timeline-img {
        @apply h-20 w-20 object-cover bg-cyan-500 rounded-full relative shadow-md border-4 border-white xs:absolute left-2 md:mx-auto md:left-0 md:right-0 ;
    }
    .timeline-container {
        @apply relative pt-2 xs:pl-28 xs:pt-0 md:w-1/2 md:ml-auto md:pl-16;
    }
    .timeline-container-left {
        @apply md:ml-0 md:mr-auto md:pl-0 md:pr-16;
    }
    .timeline-pointer {
        @apply absolute inset-0 left-10 h-4 w-4 transform rotate-45 bg-slate-800 xs:top-[33px] xs:left-26 md:left-14 group-hover:bg-slate-700 z-10;
    }
    .timeline-pointer-left {
        @apply md:left-auto md:right-14;
    }
}